import { AppBar, Toolbar, Typography } from "@mui/material";

function NavigationMenu() {

  const pages = ["Home", "Activities", "My Profile"];
  return (

    <AppBar position="static">
      <Toolbar>
        {pages.map((page) => (
          <Typography key={page}>
            {page}
          </Typography>
        ))}
      </Toolbar>
    </AppBar>

  );
}

export default NavigationMenu;