import Participant from './Participant';
import styles from './ParticipantList.module.css';

function ParticipantList(props) {
  function goToParticipantPage() {
    console.log("We are going to [name]'s page");
  }
  let participantList = props.participants.split(',');
  let listColor = participantList.length > 1 ? "green" : "red";
  return (
    <ul>
      {participantList.map((participant, index) => <li className={styles.participant} style={{ "backgroundColor": listColor }} key={index}>{participant}</li>)}
    </ul>
  );
}

export default ParticipantList;